.app-wrapper{
  margin:0 auto;
  display: grid;
  width: 100%; /* Растягиваем на всю ширину */
  max-width: 100%;
  grid-template-areas:
          'h h'
          'n c';

  grid-template-rows:60px 1fr;
  grid-template-columns:2fr 10fr;
  /* grid-gap: 10px;*/
  justify-content: center;
}





.profileWrapperContent {
  grid-area:auto;
  background-color: rgba(114, 117, 117, 0.95);
}

.impSize img{
  height: 730px;
  width: 1650px;
}





ul li {
  list-style-type:georgian ;
}
.header{
  grid-area: h;
  width: 100%;
    background-color: aqua;
}
.header img{
  width: 60px;
}
.nav {
  grid-area: n;
  background-color: #5e83e0;
}
.content {
  grid-area: c;
  background-color: #b793d9;
  display: flex;
  flex-direction: column;
  align-items: center;

}
.content ul {
  width: 100%;}

.content ul img {
  width: 100%; /* Ширина на 100% */
  height: 70%; /* Высота на 70% */
  object-fit: cover; /* Сохраняет пропорции и заполняет контейнер */
}
ul li {
  list-style-type: georgian;
  text-align: center; /* Центрируем текст */
  margin: 5px 0; /* Немного отступа для элементов списка */
}